body {
  --header-height: 60px;
  background-color: #00000029;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
}

header {
  height: var(--header-height);
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  display: flex;
  position: absolute;
}

img {
  border: 2px solid #0000;
