  padding: 0 50px 0 0;
  list-style-type: none;
  display: flex;
}

.nav_items {
  margin-right: 20px;
}

.nav_items:last-child {
  margin-right: 0;
}

.nav_links {
  color: #000;
  text-decoration: none;
  position: relative;
}

.nav_link_1, .nav_link_4 {
  color: #00acdc;
}

.nav_links:hover {
  border-bottom: 2px solid #00acdc;
